    this.date = this.$route.params.date
  },
  methods: {
    onClickLeft () {
      this.$router.isBack = true
      this.$router.goBack()
    },
    onSwipeRight () {
      this.$router.isBack = true
      this.$router.goBack()
    },
    onClickRight () {
    }
  }
}
</script>

