<template>
  <div class="plan-add-content oct-flex-col-all-center plan-main">
    <div class="oct-flex-col-all-center plan-time-picker">
      <div style="width: 100%;margin-top: 5px;" class="plan-input-box">
        <van-cell-group>
          <el-collapse-transition>
            <div v-show="showCal">
              <van-calendar
                class="van-calendar"
                id="popCanlender"
                color="rgb(165, 145, 108)"
                type="range"
                @confirm="onConfirm"
                :poppable="false"
                :max-date="maxDate"
                :min-date="minDate"
                :show-confirm="false"
                :style="{ height: '390px' }">
              </van-calendar>
            </div>
          </el-collapse-transition>
          <van-cell is-link title="阶段区间" :label="timeLabel" :value="$moment(startTime).format('YYYY/MM/DD ') + ' - ' +$moment(endTime).format('YYYY/MM/DD ')" @click="showCalender"></van-cell>
          <van-cell title="学习科目" :value="subject" is-link arrow-direction="down" @click="selectSubject = true"></van-cell>
          <van-field
            v-model="content"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="200"
            placeholder="请输入备注"
            show-word-limit
          ></van-field>
          <el-button class="plan-add-btn" @click="addPlan" type="text">{{changeMode ? '修改' : '添加'}}</el-button>
        </van-cell-group>
      </div>
    </div>
    <div style="width: 100%;margin-top: 10px;border-radius: 10px;overflow-y: auto;max-height: 100vh;min-height: 100vh">
      <oct-all-plan :plans="plans" @reload="initData"></oct-all-plan>
    </div>
    <van-action-sheet v-model="selectSubject" :actions="subjects" @select="onSelect"></van-action-sheet>
  </div>
</template>

<script>
import { savePlan, getPlanByPage, changePlan } from '@/api/study'
import OctAllPlan from '@/components/plan/OctAllPlan'

export default {
  name: 'OctAddPlan',
  components: { OctAllPlan },
  mounted () {
    // this.initData()
    if (this.$route.params.mode === 'change') {
      console.log(this.$route.params.plan)
      this.plan = this.$route.params.plan
      this.changeMode = true
      this.startTime = new Date(this.plan.startTime)
      this.endTime = new Date(this.plan.endTime)
      this.content = this.plan.content
      this.subject = this.plan.subject
    }
    this.moveToday()
  },
  activated () {
    this.moveToday()
  },
  data () {
    return {
      changeMode: false,
      plan: {},
      showCal: false,
      maxDate: new Date(new Date().getFullYear() + 1, 12, 31),
      minDate: new Date(new Date().getFullYear() - 1, 1, 1),
      date: new Date(),
      timeLabel: '结束时间请大于起始时间',
      total: 0,
      content: '',
      selectSubject: false,
      subject: '高数',
      subjects: [
        { name: '高数' },
        { name: '英语' },
        { name: '专业课' }
      ],
      startTime: new Date(),
      endTime: new Date(),
      page: {
        startPage: -1,
        pageNum: 5
      },
      plans: [],
      loading: false,
      finished: false
    }
  },
  methods: {
    async initData () {
      await getPlanByPage(this.page)
        .then((res) => {
          this.plans = []
          if (res.data.data.length > 0) {
            // this.total = res.data.data[0].total
            let result = res.data.data
            result.forEach((item) => {
              // if (this.plans.find((plan) => plan.planId === item.planId) === undefined) {
              if (item.type === 2 && this.$moment(new Date(item.startTime)).format('YYYYMM') === this.$moment(this.date).format('YYYYMM')) {
                this.plans.push(item)
              }
              // }
            })
          }
          // // 加载状态结束
          // this.loading = false
          // // 数据全部加载完成
          // if (res.data.data.length === 0) {
          //   this.finished = true
          // } else {
          //   this.page.startPage += 1
          // }
        })
    },
    // nextPage (isLoading, isFinish) {
    //   this.loading = isLoading
    //   this.finished = isFinish
    //   this.initData()
    // },
    showCalender () {
      this.showCal = true
      setTimeout(function () {
        this.moveToday()
      }.bind(this), 1)
    },
    moveToday () {
      document.getElementsByClassName('van-calendar__body')[0].scrollTop = (document.getElementsByClassName('van-calendar__month')[11].offsetTop - 80)
    },
    dateValidate () {
      if (this.$moment(this.startTime).format('YYYYMMDD') === this.$moment(this.endTime).format('YYYYMMDD')) {
        this.$dom.getElByClassName(this, 'van-cell__label')[0].style = 'color: rgba(139, 0, 0, 0.8) !important;'
        return false
      } else {
        this.$dom.getElByClassName(this, 'van-cell__label')[0].style = 'color: #969799 !important;'
        return true
      }
    },
    async addPlan () {
      if (this.dateValidate()) {
        if (!this.changeMode) {
          const data = {
            startTime: this.startTime,
            endTime: this.endTime,
            content: this.content,
            subject: this.subject,
            type: 2,
            status: 1
          }
          await savePlan(data)
            .then(() => {
              this.showCal = false
              this.plans.push(data)
            })
        } else {
          const data = {
            startTime: this.startTime,
            endTime: this.endTime,
            content: this.content,
            subject: this.subject,
            type: 2,
            status: this.plan.status,
            planId: this.plan.planId
          }
          await changePlan(data)
            .then(() => {
              this.$notify.success('修改成功!')
              this.showCal = false
              this.$router.isBack = true
              this.$router.goBack()
            })
        }
      }
    },
    onConfirm (date) {
      const [start, end] = date
      this.startTime = start
      this.endTime = end
    },
    convertDate (data) {
      return new Date(this.date.getFullYear(), this.date.getMonth(), this.date.getDate(), data.split(':')[0], data.split(':')[1])
    },
    handleEndDateConfirm (data) {
    },
    handleCancel () {
    },
    handleChange () {
    },
    onSelect (data) {
      this.subject = data.name
      this.selectSubject = false
    }
  }
}
</script>

<style lang="scss">
  .van-calendar__month-mark {
    color: rgba(165,145,108,0.2) !important;
  }
  .plan-main {
    padding: 10px;
    .van-calendar {
      background-color: rgba(248, 246, 242, 0.1);
      border-radius: 10px;
      margin: 5px 10px 10px 10px;
    }
    .van-calendar__header-title {
      display: none !important;
    }
    .van-calendar__month-title {
      margin-left: 20px;
      text-align: left !important;
      font-size: $large-font;
      font-weight: bold;
    }
  }
  .plan-input-box {
    .van-cell_red {
      color: rgba(139, 0, 0, 0.8) !important;
    }
  }
  .van-picker-column__item--selected {
    font-size: 24px !important;
  }
  .van-picker {
    background-color: $bg-color;
    width: 40% !important;
    border-radius: 10px;
  }
  .van-ellipsis {
    background-color: $bg-color;
  }
  .van-picker__columns {
    border-radius: 10px;
  }
  .van-picker-column {
    border-radius: 10px;
  }
  .van-picker__mask {
    border-radius: 10px;
  }
  .van-cell-group {
    background-color: $bg-color !important;
  }
  .van-cell {
    background-color: $bg-color !important;
  }
  .van-cell-group {
    border-radius: 10px;
    border-bottom: none;
  }
  .van-field {
    border-radius: 10px;
  }
</style>
<style scoped lang="scss">
  .plan-subject {
    background-color: $bg-color;
  }
  .plan-add-content {
    padding: 10px;
  }
  .plan-time-picker {
    width: 100%;
    border-radius: 10px;
    background-color: $bg-color;
  }
  .plan-picker-title {
    margin: 10px;
    font-size: 14px;
  }
  .plan-add-btn {
    width: 100%;
    background-color: rgba(125, 105, 78, 0.8);
    color: white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }

</style>
